.dropdown__content-field {
  position: absolute;
  z-index: 2;
  width: 100%;
  display: none;

  border: 1px solid $input-color-focus;
  border-top: 0;
  border-radius: 0 0  4px 4px;
  background-color: var(--body-bacground-color);
  box-sizing: border-box;
}
