.input-text__wrap {
	background: var(--body-bacground-color);
	border: 1px solid $color-Dark25;
	box-sizing: border-box;
	border-radius: calc(4 / 14 * 1em);
	&:hover {
		border-color: $color-Dark50;
	}
}
