//        - input-text__input,  ,
// , , , @media-...
// : http://nicothin.github.io/idiomatic-pre-CSS/#priority

.input-text__input {
	@include font-body;
	width: calc(100% - 15 / 14 * 2em);
	border: 0;
	padding: 0;
	line-height: 18/14 * 1em;
	margin: 12/14 * 1em 15/14 * 1em 12/14 * 1em 15/14 * 1em;
	box-sizing: border-box;
	color: $color-Dark25; //TODO    
	&:hover,
	&:focus {
		line-height: 24/14 * 1em;
		margin-top: 11/14 * 1em;
		margin-bottom: 7/14 * 1em;
		color: $color-Dark75;
	}
}
