//        - navigation,  ,
// , , , @media-...
// : http://nicothin.github.io/idiomatic-pre-CSS/#priority

.navigation {
  display: flex;
  justify-content: space-between;
  list-style: none;
  @include font-nav-menu;
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  margin-block-start: 0;
  margin-block-end: 0;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  padding-inline-start: 0;

}
